<script>
import { ref, onMounted } from 'vue'
import { useRouter } from "vue-router"
import { Toast } from '../../components/global/swal'
import { errorHandle } from '../../utils/handle'
import NoData from "../../components/global/NoData.vue"
import { apiGetResumeList, apiCheckProfile } from "@/api/api"

export default {
    setup() {
        const router = useRouter()
        const resumeData = ref([])

        onMounted(async () => {
            try {
                let msg = await apiCheckProfile()
                if (msg.status == false) {
                    let errMessage = errorHandle(msg)
                    Toast(errMessage)
                    router.push({ path: '/profile' })
                } else {
                    resumeData.value = await apiGetResumeList()
                    resumeData.value = resumeData.value.data
                    // console.log(resumeData.value)
                    if (resumeData.value.length == 0) {
                        Toast("目前暫無資料")
                    }
                }
            } catch (error) {
                console.log(error)
            }

        })

        return { resumeData }
    },
    components: { NoData },
};
</script>

<template>
    <div class="resumeList">
        <ul class="list-group list-group-flush">
            <NoData v-if="resumeData.length == 0"/>
            <div class="listItem" v-for="item in resumeData" :key="item.id">
                <button type="button" class="list-group-item list-group-item-action"
                    @click="$router.push({ path: `/resume/info/${item.id}` })">
                    {{ item.title }}
                </button>
            </div>
        </ul>
    </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.resumeList {
    margin-top: $header-height;
}
</style>